import React from "react";
import { Container, Row, Col } from "react-bootstrap";

const Layout = () => (
  <Container style={{ zIndex: "-1", position: "relative" }}>
    <Row className="justify-content-md-center">
      <Col>
        Она пришла под утро. Вошла осторожно, тихо, бесшумно ступая, плывя по
        комнате, словно призрак, привидение, а единственным звуком, выдававшим
        ее движение, был шорох накидки, прикасавшейся к голому телу. Однако
        именно этот исчезающе тихий, едва уловимый шелест разбудил ведьмака, а
        может, только вырвал из полусна, в котором он мерно колыхался, словно
        погруженный в бездонную топь, висящий между дном и поверхностью
        спокойного моря, среди легонько извивающихся нитей водорослей. Он не
        пошевелился, даже не дрогнул. Девушка подпорхнула ближе, сбросила
        накидку, медленно, нерешительно оперлась коленом о край ложа. Он
        наблюдал за ней из–под опущенных ресниц, не выдавая себя. Девушка
        осторожно поднялась на постель, легла на него, обхватила бедрами.
        Опираясь на напряженные руки, скользнула по его лицу волосами. Волосы
        пахли ромашкой. Решительно и как бы нетерпеливо наклонилась, коснулась
        сосочком его века, щеки, губ. Он улыбнулся, медленно, осторожно, нежно
        взял ее руки в свои. Она выпрямилась, ускользая от его пальцев,
        лучистая, подсвеченная и от этого света нечеткая в туманном отблеске
        зари. Он пошевелился, но она решительным нажимом обеих рук остановила
        его и легкими, но настойчивыми движениями бедер добилась ответа. Он
        ответил. Она уже не избегала его рук, откинула голову, встряхнула
        волосами. Ее кожа была холодной и поразительно гладкой. Глаза, которые
        он увидел, когда она приблизила свое лицо к его лицу, были огромными и
        темными, как глаза русалки. Покачиваясь, он утонул в ромашковом море, а
        оно взбурлило и зашумело, потеряв покой. Ведьмак 1 Потом говорили, что
        человек этот пришел с севера, со стороны Канатчиковых ворот. Он шел, а
        навьюченную лошадь вел под уздцы. Надвигался вечер, и лавки канатчиков и
        шорников уже закрылись, а улочка опустела. Было тепло, но на человеке
        был черный плащ, накинутый на плечи. Он обращал на себя внимание. Путник
        остановился перед трактиром «Старая Преисподняя», постоял немного,
        прислушиваясь к гулу голосов. Трактир, как всегда в это время, был полон
        народу. Незнакомец не вошел в «Старую Преисподнюю», а повел лошадь
        дальше, вниз по улочке к другому трактиру, поменьше, который назывался
        «У Лиса». Здесь было пустовато — трактир пользовался не лучшей
        репутацией. Трактирщик поднял голову от бочки с солеными огурцами и
        смерил гостя взглядом. Чужак, все еще в плаще, стоял перед стойкой
        твердо, неподвижно и молчал. — Что подать? — Пива, — сказал незнакомец.
        Голос был неприятный. Трактирщик вытер руки полотняным фартуком и
        наполнил щербатую глиняную кружку. Незнакомец не был стар, но волосы у
        него были почти совершенно белыми. Под плащом он носил потертую кожаную
        куртку со шнуровкой у горла и на рукавах. Когда сбросил плащ, стало
        видно, что на ремне за спиной у него висит меч. Ничего странного в этом
        не было, в Вызиме почти все ходили с оружием, правда, никто не носил меч
        на спине, словно лук или колчан. Незнакомец не присел к столу, где
        расположились немногочисленные посетители, а остался у стойки,
        внимательно изучая взглядом трактирщика. — Ищу комнату на ночь, —
        проговорил он, отхлебнув из кружки. — Нету, — буркнул трактирщик, глядя
        на обувку гостя, запыленную и грязную. — Спросите в «Старой
        Преисподней». — Я бы хотел здесь. — Нету. — Трактирщик наконец распознал
        выговор незнакомца. Ривянин. — Я заплачу, — тихо и как бы неуверенно
        сказал чужак. Тогда–то и началась эта паскудная история. Рябой верзила,
        с момента появления чужака не спускавший с него угрюмого взгляда, встал
        и подошел к стойке. Двое из его дружков встали шагах в двух позади. —
        Ну, нету же местов, шпана ривская, — гаркнул рябой, подходя к незнакомцу
        вплотную. — Нам тута, в Вызиме, такие ни к чему. Это порядочный город.
        Незнакомец взял свою кружку и отодвинулся. Взглянул на трактирщика, но
        тот отвел глаза. Он и не думал защищать ривянина. Да и кто любит ривян?
        — Что ни ривянин, то ворюга, — продолжал рябой, от которого несло пивом,
        чесноком и злобой. — Слышь, что говорю, недоносок? — Не слышит он,
        уши–то дерьмом забил, — промямлил один из тех, что стояли позади. Второй
        захохотал. — Плати и выматывайся, — рявкнул рябой. Только теперь
        незнакомец взглянул на него. — Пиво допью. — Мы те подмогнем, — прошипел
        дылда. Он выбил у ривянина кружку и, одновременно схватив одной рукой
        плечо, впился пальцами другой в ремень, пересекающий наискось грудь
        чужака. Один из стоявших позади размахнулся, собираясь ударить. Чужак
        развернулся на месте, выбив рябого из равновесия. Меч свистнул в ножнах
        и коротко блеснул в свете каганцев. Пошла кутерьма. Поднялся крик.
        Кто–то из гостей кинулся к выходу. С грохотом упал стол, глухо
        шмякнулась об пол глиняная посуда. Трактирщик — губы у него тряслись —
        глядел на чудовищно рассеченное лицо рябого, а тот, вцепившись пальцами
        в край стойки, медленно оседал, исчезал из глаз, будто тонул. Двое
        других лежали на полу. Один не двигался, второй извивался и дергался в
        быстро расплывающейся темной луже. В воздухе дрожал, ввинчиваясь в мозг,
        тонкий, истошный крик женщины. Трактирщик затрясся, хватил воздуха, и
        его начало рвать. Незнакомец отступил к стене. Сжавшийся, собранный,
        чуткий. Меч он держал обеими руками, водя острием по воздуху. Никто не
        шевелился. Страх, как холодная грязь, облепил лица, связал члены,
        заткнул глотки. В трактир с шумом и лязгом ворвались трое стражников.
        Видимо, находились неподалеку. Обернутые ремнями палицы были наготове,
        но, увидев трупы, стражи тут же выхватили мечи. Ривянин прильнул спиной
        к стене, левой рукой вытащил из–за голенища кинжал. — Брось! — рявкнул
        один из стражников дрожащим голосом. — Брось, бандюга! С нами пойдешь!
        Второй толкнул стол, мешавший ему зайти ривянину сбоку. — Жми за людьми,
        Чубчик! — крикнул он тому, что стоял ближе к двери. — Не надо, —
        проговорил незнакомец, опуская меч. — Сам пойду. — Пойдешь, пойдешь,
        сучье племя, только на веревке! — заорал тот, у которого дрожал голос. —
        Кидай меч, не то башку развалю! Ривянин выпрямился. Быстро перехватил
        меч под левую руку, а правой, выставив ее в сторону стражников, начертил
        в воздухе сложный знак. Сверкнули набивки, которыми были густо покрыты
        длинные, по самые локти, манжеты кожаной куртки. Стражники моментально
        отступили, заслоняя лица предплечьями. Кто–то из гостей вскочил, другой
        помчался к двери. Женщина снова завопила. Дико, пронзительно. — Сам
        пойду, — повторил незнакомец звучным металлическим голосом. — А вы трое
        — впереди. Ведите к ипату. Я дороги не знаю.
      </Col>
    </Row>
  </Container>
);
export default Layout;
